<template>
<div>
    <div class="tree">
        <div class="userRole">
            <p>当前用户角色</p>
            <div v-if="userRole.length > 0">
                <ul class="allRole">
                <li v-for="(item, index) in userRole" :key="index" class="itemRole">
                    {{item.roleName}}
                </li>
            </ul>
            </div>
            <div v-else class="none">暂无角色</div>
        </div>
        <div class="userRole">   
            <p>所有角色</p>
            <ul class="allRole">
                <li v-for="(item, index) in allRole" :key="index" class="itemRole">
                     <el-checkbox :label="item.roleName" :value='item.roleId' v-model="checkBoxRule"></el-checkbox>
                </li>
            </ul>
        </div>
    </div>
    <div class="submitBtn">
        <el-button type="primary" @click="submitMenu">提交</el-button>
    </div>
</div>
</template>

<script>
import { get_user_geRoles_api, get_role_list_api, get_user_saveRoles_api } from '@/api/system';
export default {
    data() {
        return {
            checkBoxRule: [], // 多选角色
            sameList: [], // 已存在的菜单
            allRole: [], // 所有角色
            userRole: [], // 用户角色
        }
    },
    methods: {
        getData () {
            Promise.all([
                get_user_geRoles_api({userId:this.$route.params.id}),
                get_role_list_api({pageSize:1000,pageNo:1})
            ]).then(res => {
                
                this.userRole = res[0];
                this.userRole.forEach(ele => {
                    this.checkBoxRule.push(ele.roleName);
                });
                this.allRole = res[1].list;
            });
        },
        submitMenu () {
            let arr = [];
            this.checkBoxRule.forEach(item => {
                this.allRole.forEach(allItem => {
                    if (item == allItem.roleName) {
                        arr.push(allItem.roleId);
                    } 
                });
            });
            let obj = {
                roleIds: arr,
                userId: this.$route.params.id
            }
            get_user_saveRoles_api(obj).then(() => {
                this.$message({
                    message: "修改成功",
                    type: 'success'
                })
                this.getData();
            })
        }
    },
    created() {
        this.getData();
    }
}
</script>

